
.category {
  margin: 15px 5px 5px 5px;
  height: 100%;
  width: 10%;
  position: fixed;
  z-index: 1;
  left: 0px;
}
.withoutcategory {
  margin-left: 2%;
}
.content {
  margin-left: 13%;
}
body {
  font: normal 16px "Trebuchet MS", Helvetica, sans-serif;
  margin: 0;
}
.fullscreen {
  width: 95%;
  height: 100%;
  z-index: 1;
  overflow-x: hidden;
  margin-top : 15px;
}
.split {
  width: 43%;
  height: 100%;
  position: fixed;
  z-index: 1;
  overflow-x: hidden;
  margin-top : 15px;
}
.splitH {
  width: 95%;
  height: 50%;
  z-index: 1;
  overflow-x: hidden;
  margin-top : 15px;
}
.bottom {
  top: 50%;
}
.top {
  height: 350px;
}
.right {
  right: 0px;
}


.new {
 background-color: aliceblue;
}
.done {
 background-color: lightsteelblue;
 color: grey;
}
.done ul {
 text-decoration: line-through
}
.empty {
  color : white;
  background-color : #f0f0f5;
}
.input {
  padding: 5px 0px 5px 5px;
  font-size: medium;
}
.title {
  width: 98%;
  margin: 0px 0px 5px 0px;
  border-radius: 5px;
  border-width: 0px;
}
.desc {
  width: 98%;
  margin: 0px 0px 0px 0px;
  border-radius:5px;
  border-width:0px;
}
.listTask .task-row {
  padding : 2px 2px;
  border-bottom: 1px solid;
}
.task-desc {
  font-size: 13;
  padding-left: 10px;
  color: grey;
}
.task-title {
  font-size: 15;
}


.element {
  padding: 10px 0px 10px 10px;
  border-radius: 5px;
  width : 100%;
}
.selected {
 background-color: aliceblue;
}
.default {
  background-color : #f0f0f5;
}


.i-category {
  margin: 5px 0px 0px 5px;
}

